import React from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import classNames from "classnames"

const NavListLink = props => (
  <li>
    <Link to={props.to}>{props.children}</Link>
  </li>
)

class Header extends React.Component {
  constructor(props) {
    super(props)

    this.state = { sidebar_visible: false }

    this.handleMenuToggle = this.handleMenuToggle.bind(this)
  }

  handleMenuToggle() {
    this.setState(state => (
      { sidebar_visible: !state.sidebar_visible }
    ))
  }

  render() {
    let panelClasses = classNames("panel", {
      "visible": this.state.sidebar_visible
    })

    return (
      <header>
        <div className="content">
          <h2 className="brand">
            <Link to="/">{this.props.siteTitle.toLowerCase()}</Link>
          </h2>
          {/* top navigation (tablet portrait up) */}
          <nav className="top">
            <ul>
              <NavListLink to="/">home</NavListLink>
              <NavListLink to="/about/">about</NavListLink>
              <NavListLink to="/projects/">projects</NavListLink>
            </ul>
          </nav>
          {/* sidebar navigation (phone only) */}
          <nav className="side">
            <button
              className="menu-toggle"
              onClick={this.handleMenuToggle}
            >
              {this.state.sidebar_visible ? "close" : "menu"}
            </button>
            <div className={panelClasses}>
              <ul>
                <NavListLink to="/">home</NavListLink>
                <NavListLink to="/about/">about</NavListLink>
                <NavListLink to="/projects/">projects</NavListLink>
              </ul>
            </div>
          </nav>
        </div>
      </header>
    )
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
